        }
        .small_menu{
            padding-left: 14px;
            display: none;
        }
        .submenu{
            cursor : pointer;
        }
        
        .back{
            width:20px;
            position: fixed;
            top:0px;
            left: 0px;
            height:100%;
            line-break:anywhere;
            word-break: break-all;
            font-size:9px;
            z-index: 4000;
        }
        .back_link{
            color:black;
            text-decoration-line: none;
        }
        #shade_div{
            background: #e0d6f1;
            opacity: 1;
            position: fixed;
            top:0px;
            left:0px;
            width: 100%;
            height:100%;
            z-index: 100000;
        }
        .shade_animation{
            animation-name: shade;
            animation-duration: 1s;
            animation-fill-mode: forwards;
            animation-delay: 1s;
        }
        @keyframes shade {
          from {opacity: 1;z-index: 100000;display: inherit;}
          to {opacity: 0; z-index: -100;display: none;}
        }
        #table {

            width: 100%;
            border-collapse: collapse;
            border-top: 1px solid black;
            /*border-bottom: 1px solid black;*/
        }
        #table_div{
            width: 100%;
            /*display:none;*/
            position:absolute;
            bottom:0;
            left:0;
            font-size: 10px;
            z-index: 30000;

        }
        th, td {
          padding: 1px;
          padding-left: 7px;
          text-align: left;
            border-bottom: 1px solid black; 
        
        }
        tr:hover{
            background-color: black;
            color:white;
        }
